import React, { useEffect, useState } from "react";



import { getStore, updStore, gen16CodeId, compareJSON, fetchPostH, getUrlServerSide, ObjectKeys, parseDateMMDDYYYY, parseDateMMYYYY, _monthNames } from "../../../stores/Utils";
import { generateTranslateDriverLicense } from "../services";
import {  TextInput_Cmpt } from "../../../components";
import { DateCalComponent_Cmpt } from "../../Inventory";
import { CloseModal } from "../../../components/DialogHRM";


const DateCalComponent =DateCalComponent_Cmpt();


const TextInput = TextInput_Cmpt();





const frmNm = "driver_licence_translate"


let flds = [
	"dl_number",
	"dl_issues",
	"dl_exire",
	"dl_type",
	"endorsment",
	"restriction"
]

const TranslateDL  = ({ data, elmId, close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);



 
	const loadDoc  = async ()  => {
		let bdyq2 = {
			query: "getScanDocumentsNotary",
			params: {
				":customerId" : getStore("upd_client").clientNotaryId,
				":documentType": "DLT_Aff"
			},
			queryString: "customerId contain :customerId AND documentType contain :documentType",
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && !td.error) {

				let docId = ObjectKeys(td.data)[0];

				if(td.data[docId]){
					updStore(frmNm, td.data[docId]);
					updStore(frmNm + "_bck", JSON.parse(JSON.stringify(td.data[docId])));
					set_obs(gen16CodeId())
				}
				else{
					updStore(frmNm, null);
					set_obs(gen16CodeId())
				}
			
		}

	}


	
	useEffect(() => {
		if (!initialize) {
			
			setInitialize(true);
			updStore(frmNm, null);
			set_obs(gen16CodeId());

			setTimeout(() => {
				loadDoc();
				let user = {...getStore("upd_client")};
				let frm_userInfo = {
					"dob":	user?.dob,
					"dateOfBirth": user?.dateOfBirth,
					"name":user?.firstName ? `${user?.firstName} ${user?.middleName?user?.middleName+" ":""}${user?.lastName} ` :"",
					"ss": user?.ss
				};
			
				updStore(frmNm+"_userInfo", frm_userInfo);
				set_obs(gen16CodeId())
			}, 50);
			return () => {
				updStore(frmNm, null);
			}; 
		}
	});

	const updFrm = (f, v) => {
		let frm2 = getStore(frmNm) || {};
		frm2[f] = v;
		updStore(frmNm, frm2);
		set_obs(gen16CodeId())
	};



	const save  = async ()  => {

		let ffm =  getStore(frmNm);

		let bdyq2 = {};
		if(ffm["documentNotaryId"]){
			let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )
		
			bdyq2 = {
				query: "updateDocumentsNotary",
				params:{
					"documentNotaryId": ffm["documentNotaryId"]
				},
				data2update: diff.data
			};
		}
		else{
			ffm["customerId"] = getStore("upd_client").clientNotaryId;
			ffm["documentType"] = "DLT_Aff";
			ffm["documentNotaryId"] = gen16CodeId();
			bdyq2 = {
				query: "addDocumentsNotary",
				form:ffm
			};
		}

		// * Class B Automovile (Mar 2010), C: Truck over 7500KG (Jun 2014)
	
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			updStore(frmNm, td.data);
			set_obs(gen16CodeId())
			data?.modalID && CloseModal({id:data?.modalID});
		
		}
	}


	const print  = async ()  => {
		let ffm = Object.assign({}, getStore(frmNm),getStore(frmNm+"_userInfo"));

		
		if(ffm["name"]){
			ffm["name_jury"] = ffm["name"];
		}

		if(ffm["dateOfBirth"]){
			ffm["dob_p"] = parseDateMMDDYYYY(ffm["dateOfBirth"]);
		}

		if(ffm["dl_issues"]){
			ffm["dl_issue_p"] = parseDateMMYYYY(ffm["dl_issues"]);
		}

		if(ffm["dl_exire"]){
			ffm["dl_exp_p"] = parseDateMMYYYY(ffm["dl_exire"]);
		}

		if(ffm["ss"]){
			ffm["SS"] = parseSS(ffm["ss"]);
		}else{
			ffm["SS"] = "N/A";
		}

		
	
		
		let tDdate = ffm["translateDate"] ? (new Date(ffm["translateDate"]))  : (new Date());

		let DD = tDdate.getDate();
		let YY = tDdate.getFullYear().toString().slice(2,4);



		ffm["inter_date"] =  parseDateMMDDYYYY(tDdate.getTime());



		ffm["day_en_jury"] = DD;
		ffm["day_es_jury"] = DD;
		ffm["half_year_en_jury"] = YY;
		ffm["half_year_es_jury"] = YY;

		let MM = tDdate.getMonth()+1;

		ffm["month_en_jury"] = _monthNames["en"][MM];
		ffm["month_es_jury"] = _monthNames["es"][MM];
		
		

		let pdfUri = await generateTranslateDriverLicense(ffm, true, true);
		window.open(pdfUri);
	};

	

	return (
		<div style={{
			maxHeight: "80vh",
			width: "800px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Traducion de Licencia
				</p>
				<div className="flexSpace"/>
				<button  className="group_title addBtn purple" onClick={print}>
					Imprimir
				</button>
			</div>
			
			
			<p className="group_title">
			</p>
		
			
			<div className="_dsplFlx spaceAround"  style={{marginTop:20}}> 
				<TextInput  init={getStore(frmNm)?.["dl_number"]} 
					label={"Numero de Licencia"} 
					width={160} updChanges={(e)=>updFrm("dl_number",e)}
				/>
				<DateCalComponent  
					init={getStore(frmNm)?.["dl_issues"]} 
					label={"Fecha de emicion"}
					updDate={(e)=>updFrm("dl_issues",e)}
				/>
				<DateCalComponent  
					init={getStore(frmNm)?.["dl_exire"]} 
					label={"Fecha de expiracion"}
					updDate={(e)=>updFrm("dl_exire",e)}
				/>
			</div>
			<div className="_dsplFlx spaceAround flxWrp"> 
				<TextInput  init={getStore(frmNm)?.["dl_type"]} label={"Tipo de Licencia"} width={650} updChanges={(e)=>updFrm("dl_type",e)}/>
				<TextInput  
					init={getStore(frmNm)?.["endorsment"]} 
					label={"Aprobaciónes"} 
					width={650} 
					updChanges={(e)=>updFrm("endorsment",e)}
				/>
				<TextInput  
					init={getStore(frmNm)?.["restriction"]} 
					label={"Restriciones"} 
					width={650} 
					updChanges={(e)=>updFrm("restriction",e)}
				/>
			</div> 
			
			<div className="_dsplFlx spaceAround" style={{marginTop:20}}>
				<DateCalComponent  
					init={getStore(frmNm)?.["translateDate"]} 
					label={"Fecha de la traduccion"}
					updDate={(e)=>updFrm("translateDate",e)}
				/>
			</div>

			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<button className="group_title purple addBtn" onClick={save}>
					Guardar
				</button>
			</div>
		
		</div>
	);
};

export default TranslateDL;

/*********************************************************************************************************************************************/
/**************************************   loadAuth load auht function and render google singin button ****************************************/
/*********************************************************************************************************************************************/


const parseSS = (v) =>{
	if(v){
		let f = v.substring(0,3) + " - "+ v.substring(3,5) + " - "+v.substring(5,9);
		return f;
	}
	return ""

}